<template>
  <div class="container">
    <wux-cell-group>
      <wux-cell hover-class="none">
        <wux-input
          label="位置名称"
          id="name"
          :value="form.name"
          placeholder="请输入位置名称"
          controlled
          @change="onChange"
        />
      </wux-cell>
    </wux-cell-group>

    <wux-button block type="positive" class="device_button" @click="Send">保存</wux-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        devEui: "",
        name: "",
        img_url: "",
        appKey: "2B7E151628AED2A6ABF7158866554433",
        rule_id: 7
      }
    };
  },
  methods: {
    Send() {
      this.request("device/addDevice", this.form, "post")
        .then(res => {
          console.log(res);
        })
        .catch(e => {});
    },
    onChange(e) {
      this.form[e.mp.currentTarget.id] = e.mp.detail.value;
    },
    GetId() {
      if (this.$route.query.devEui) {
        this.form.devEui = this.$route.query.devEui;
      }
      if (this.$route.query.appKey) {
        this.form.appKey = this.$route.query.appKey;
      }
      if (this.$route.query.edit) {
        wx.setNavigationBarTitle({
          title: "设备修改"
        });
      } else {
        wx.setNavigationBarTitle({
          title: "添加设备"
        });
      }
    },
    GetData() {}
  },
  mounted() {
    this.GetId();
  }
};
</script>

<style scoped>
.device_button {
  position: fixed;
  width: 80%;
  bottom: 5%;
  left: 10%;
}
</style>



